.App {
  text-align: center;
  padding: 40px;
  font-family: 'Noto Sans KR', sans-serif;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
  margin-top: 30px;
}

.card-thumb {
  cursor: pointer;
  transition: transform 0.3s;
}

.card-thumb img {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #ffeaa7;
}

.card-thumb:hover {
  transform: scale(1.05);
}

.card {
  max-width: 500px;
  margin: 0 auto;
  background: #fff3e0;
  padding: 30px;
  border-radius: 20px;
  box-shadow: 0 4px 16px rgba(0,0,0,0.1);
}

.main-img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 20px;
  margin-bottom: 20px;
}

button {
  margin-top: 20px;
  padding: 12px 24px;
  font-size: 16px;
  border: none;
  background-color: #ffb347;
  border-radius: 10px;
  cursor: pointer;
}
